import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginSignupComponent } from './login-signup/login-signup.component';
import { HomeComponent } from './home/home.component';
import { PatientsComponent } from './patients/patients.component';
import { PatientInfoComponent } from './patient-info/patient-info.component';
import { CasePicturesComponent } from './case-pictures/case-pictures.component';
import { CaseDetailsComponent } from './case-details/case-details.component';
import { AppointmentComponent } from './appointment/appointment.component';
import { PreviousCaseDetailsComponent } from './previous-case-details/previous-case-details.component';

import { AuthGuard } from './guard/auth.guard';
import { RouteDeactiveGuard } from './guard/route-deactive.guard'

const routes: Routes = [
  {path:'',component:HomeComponent,canActivate:[AuthGuard]},
  {path:'login',component:LoginSignupComponent,canActivate:[RouteDeactiveGuard]},
  {path:'signup',component:LoginSignupComponent,}
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [LoginSignupComponent,HomeComponent,PatientsComponent,PatientInfoComponent,CasePicturesComponent,CaseDetailsComponent,AppointmentComponent,PreviousCaseDetailsComponent]
