import { Component, OnInit } from '@angular/core';
import { FormGroup,FormBuilder,Validators } from '@angular/forms';
import { AuthService } from '../service/auth.service'

@Component({
  selector: 'app-case-details',
  templateUrl: './case-details.component.html',
  styleUrls: ['./case-details.component.css'],
  inputs: ['patient']
})
export class CaseDetailsComponent implements OnInit {

  display:Number = 0
  previousDrsCaseDisplay:Number = 0

  selectedPreviousDrsCase:Array<any> = []

  patient:any

  caseAvailable:Boolean = false
  drsCaseId:String

  previousDrsCaseArray:Array<any> = []

  chiefComplaintArray:Array<any> = []
  allergiesArray:Array<any> = []
  doctorNotesArray:Array<any> = []
  diagnosisArray:Array<any> = []
  prescribedLabTestsArray:Array<any> = []

  reportFile:Array<File>

  prescriptionForm:FormGroup
  feq1:Number = 0
  feq2:Number = 0
  feq3:Number = 0
  feq4:Number = 0

  prescriptionArray:Array<any> = []

  constructor(private formBuilder:FormBuilder,private authService:AuthService) { }

  previousCaseDetail(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[0].classList.add('icon-active')
    
    this.display = 0
    this.previousDrsCaseDisplay = 0
  }

  chiefComplaints(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[1].classList.add('icon-active')
    this.display = 1
    const textBox:any = document.getElementById('textBox')
    if(textBox){
      textBox.value = ''
    }
  }

  allergies(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[2].classList.add('icon-active')
    this.display = 2
    const textBox:any = document.getElementById('textBox')
    if(textBox){
      textBox.value = ''
    }
  }

  doctorNotes(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[3].classList.add('icon-active')
    this.display = 3
    const textBox:any = document.getElementById('textBox')
    if(textBox){
      textBox.value = ''
    }
  }

  diagnosis(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[4].classList.add('icon-active')
    this.display = 4 
    const textBox:any = document.getElementById('textBox')
    if(textBox){
      textBox.value = ''
    }
  }

  prescription(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[5].classList.add('icon-active')
    this.display = 5
  }

  prescribedLabTest(){
    var stepper = document.getElementsByClassName('stepper-icon')
    for(var i=0;i<stepper.length;i++){
      stepper[i].classList.remove('icon-active')
    }
    stepper[6].classList.add('icon-active')
    this.display = 6
    const textBox:any = document.getElementById('textBox')
    if(textBox){
      textBox.value = ''
    }
  }


  choosePreviousDrsCase(previousDrsCase:any){
    this.selectedPreviousDrsCase = previousDrsCase
    this.previousDrsCaseDisplay = 1
    console.log('selected',this.selectedPreviousDrsCase)
  }


  addTextToArray(){
    const textBox:any = document.getElementById('textBox')
  
    if(this.display == 1){

      if(this.caseAvailable){
        console.log(this.drsCaseId)
      this.authService.patchChiefComplaints(this.drsCaseId,{chiefComplaints:textBox.value}).subscribe(
        (complaint:any)=>{
          this.chiefComplaintArray = complaint.chiefComplaints
        }
      )
      }
      else{
       this.authService.postDrsCase(this.patient.patientId,{chiefComplaints:textBox.value}).subscribe(
         (complaint:any)=>{
           this.chiefComplaintArray = complaint.chiefComplaints
           this.drsCaseId = complaint._id
         }
       ) 
       this.caseAvailable = true
      }
      textBox.value = ''
    }
    else if(this.display == 2){

      if(this.caseAvailable){
      this.authService.patchAllergies(this.drsCaseId,{allergies:textBox.value}).subscribe(
        (allergie:any)=>{
        this.allergiesArray = allergie.allergies    
        }
      )
    }
    else{
      this.authService.postDrsCase(this.patient.patientId,{allergies:textBox.value}).subscribe(
        (allergie:any)=>{
          this.allergiesArray = allergie.allergies
          this.drsCaseId = allergie._id
        }
      )
      this.caseAvailable = true
    }
    textBox.value = ''
  }
    else if(this.display == 3){
      if(this.caseAvailable){
      this.authService.patchDrsNotes(this.drsCaseId,{drsNotes:textBox.value}).subscribe(
        (drsNote:any)=>{
          this.doctorNotesArray = drsNote.drsNotes
        }
      )
      }
      else{
        this.authService.postDrsCase(this.patient.patientId,{drsNotes:textBox.value}).subscribe(
          (drsNote:any)=>{
            this.doctorNotesArray = drsNote.drsNotes
            this.drsCaseId = drsNote._id
          }
        )
        this.caseAvailable = true
      }
      textBox.value = ''
    }
    else if(this.display == 4){
      if(this.caseAvailable){
      this.authService.patchDiagnosis(this.drsCaseId,{diagnosis:textBox.value}).subscribe(
        (diagnos:any)=>{
          this.diagnosisArray = diagnos.diagnosis
        }
      )
    }
    else{
      this.authService.postDrsCase(this.patient.patientId,{diagnosis:textBox.value}).subscribe(
        (diagnos:any)=>{
          this.diagnosisArray = diagnos.diagnosis
          this.drsCaseId = diagnos._id
        }
      )
      this.caseAvailable = true
    }
      textBox.value = ''
    }
    else if(this.display == 6){
      if(this.caseAvailable){
        this.authService.patchPrescribedTests(this.drsCaseId,{prescribedTests:textBox.value}).subscribe(
          (prescribedTest:any)=>{
            this.prescribedLabTestsArray = prescribedTest.prescribedTests
          }
        )
      }
      else{
        this.authService.postDrsCase(this.patient.patientId,{prescribedTests:textBox.value}).subscribe(
          (prescribedTest:any)=>{
            this.prescribedLabTestsArray = prescribedTest.prescribedTests
            this.drsCaseId = prescribedTest._id
          }
        )
        this.caseAvailable = true
      }
        textBox.value = ''
    }

  }

  removeTextToArray(index,arrayIndex){
    console.log(index,arrayIndex)

    if(arrayIndex == 0){
     this.chiefComplaintArray.splice(index,1)
     console.log(this.chiefComplaintArray)
     this.authService.removeChiefComplaints(this.drsCaseId,{chiefComplaints:this.chiefComplaintArray}).subscribe()
    }
    else if(arrayIndex == 1){
      this.allergiesArray.splice(index,1)
      this.authService.removeAllergies(this.drsCaseId,{allergies:this.allergiesArray}).subscribe()
    }
    else if(arrayIndex == 2){
      this.doctorNotesArray.splice(index,1)
      this.authService.removeDrsNotes(this.drsCaseId,{drsNotes:this.doctorNotesArray}).subscribe()
    }
    else if(arrayIndex == 3){
      this.diagnosisArray.splice(index,1)
      this.authService.removeDiagnosis(this.drsCaseId,{diagnosis:this.diagnosisArray}).subscribe()
    }
    else if(arrayIndex == 5){
          this.prescribedLabTestsArray.splice(index,1)
          this.authService.removePrescribedTests(this.drsCaseId,{prescribedTests:this.prescribedLabTestsArray}).subscribe()
    }
  }

  addFrequency(id){
  
    const frequencyIcon = document.getElementsByClassName('frequency-icon');

    if(frequencyIcon[id].classList.contains('frequency-active')){
      if(id == 0){this.feq1 = 0}
      if(id == 1){this.feq2 = 0}
      if(id == 2){this.feq3 = 0}
      if(id == 3){this.feq4 = 0}
        this.prescriptionForm.patchValue({frequency:{feq1:this.feq1,feq2:this.feq2,feq3:this.feq3,feq4:this.feq4}})
      frequencyIcon[id].classList.remove('frequency-active')
    }
    else{
      if(id == 0){this.feq1 = 1}
      if(id == 1){this.feq2 = 1}
      if(id == 2){this.feq3 = 1}
      if(id == 3){this.feq4 = 1}
      this.prescriptionForm.patchValue({frequency:{feq1:this.feq1,feq2:this.feq2,feq3:this.feq3,feq4:this.feq4}})
        frequencyIcon[id].classList.add('frequency-active')
      }

  }

  chooseReport(event){
    this.reportFile = event.target.files
  }

  addPrescription(){
  
    if(this.caseAvailable){
      this.prescriptionForm.patchValue({reportFile:this.reportFile})
    console.log(this.prescriptionForm)
    this.authService.postPrescription(this.drsCaseId,this.prescriptionForm.value).subscribe(
      (prescription:any)=>{
        this.prescriptionArray = prescription.prescriptions
      }
    )    
  }
    else{
      this.prescriptionForm.patchValue({reportFile:this.reportFile})
      this.authService.postDrsCase(this.patient.patientId,{prescriptions:this.prescriptionForm.value}).subscribe(
        (prescription:any)=>{
          this.prescriptionArray = prescription.prescriptions
          this.drsCaseId = prescription._id
          this.caseAvailable = true
        }
      )

    }
  
  }



  ngOnInit(): void {


      this.authService.getDrsCase(this.patient.patientId).subscribe(
        (drsCase:any)=>{
          if(drsCase != null){

          this.caseAvailable = true

          this.drsCaseId = drsCase._id
          this.chiefComplaintArray = drsCase.chiefComplaints
          this.allergiesArray = drsCase.allergies
          this.doctorNotesArray = drsCase.drsNotes
          this.diagnosisArray = drsCase.diagnosis
          this.prescriptionArray = drsCase.prescriptions
          this.prescribedLabTestsArray = drsCase.prescribedTests

          console.log(this.drsCaseId)
          console.log(this.caseAvailable)
          }
          else{
            console.log('case available'+this.caseAvailable)
          }
          
        }
      )


      this.authService.getPreviousDrsCase(this.patient.patientId).subscribe(
        (previousCase:any)=>{
          this.previousDrsCaseArray = previousCase
          console.log(this.previousDrsCaseArray)
        }
      )

    this.prescriptionForm = this.formBuilder.group({
      reportFile:[null,[Validators.required]],
      medicine:[null,[Validators.required]],
      dosage:[null,[Validators.required]],
      frequency:[null,[Validators.required]],
      duration:this.formBuilder.group({
        durationNumber:[null,[Validators.required]],
        durationRadio:[null,[Validators.required]],
      }),
        instructions:[null,[Validators.required]]
    })
  }

}
