import { Component, OnInit } from '@angular/core';
import { EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {AuthService} from '../service/auth.service'
@Component({
  selector: 'app-patients',
  templateUrl: './patients.component.html',
  styleUrls: ['./patients.component.css'],
  outputs:['selectedPatient']
})
export class PatientsComponent implements OnInit {

  display:number=0
  patients:Array<any> = []
  patientImage:File
  imagePreview:any
  selectedPatient = new EventEmitter()

  addPatientForm:FormGroup
  constructor(private authService:AuthService,private formBuilder:FormBuilder) { }

  openPatientForm(){
    this.display = 1
  }

  closePatientForm(){
    this.display = 0
  }
  choosePatientImage(event){
    this.patientImage = event.target.files[0]
    const reader = new FileReader()
    reader.onload = ()=>{
      this.imagePreview = reader.result;
    }
    reader.readAsDataURL(this.patientImage)
  }
  choosePatient(patient:any){
    this.selectedPatient.emit(patient)
  }

  addPatient(){

    this.addPatientForm.patchValue({patientImage:this.patientImage})
    this.authService.postPatient(this.addPatientForm.value).subscribe(
      (patient)=>{
        console.log(patient)
        this.patients.push(patient)
      }
    )
    this.patientImage = null
    this.imagePreview = null
    console.log(this.patientImage)
    this.addPatientForm.reset()
    this.closePatientForm()
  }

  ngOnInit(): void {
    this.authService.getPatient().subscribe(
      (patient:any)=>{
        console.log(patient)
        this.patients = patient
      }
    )

    this.addPatientForm = this.formBuilder.group({
        firstName:[null,[Validators.required]],
        middleName:[' '],
        lastName:[null,[Validators.required]],
        age:[null,[Validators.required]],
        gender:[null,[Validators.required]],
        mobileNumber:[null,[Validators.required]],
        bloodGroup:[null,[Validators.required]],
        apartment:[null,[Validators.required]],
        street:[null,[Validators.required]],
        city:[null,[Validators.required]],
        state:[null,[Validators.required]],
        pincode:[null,[Validators.required]],
        nationality:[null,[Validators.required]],
        patientImage:[null,[Validators.required]]

    })

  }

}
